/* fira-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/fira-sans-v10-latin_cyrillic-regular.woff2") format("woff2"); /* Super Modern Browsers */
  src: url("../fonts/fira-sans-v10-latin_cyrillic-regular.woff") format("woff"); /* Modern Browsers */
}

